import { Suspense, lazy } from "react"
import Footer from "./components/Footer"
import Navbar from "./components/Navbar"
import {BrowserRouter, Routes, Route} from "react-router-dom"
import { Toaster } from 'react-hot-toast';

const SuspenceFallback = lazy(() => import("./components/SuspenceFallback"))
const SignUpPage = lazy(() => import ("./pages/SignUpPage"))
const LoginPage = lazy(() => import ("./pages/LoginPage"))
const ForgotPassword = lazy(() => import ("./pages/ForgotPassword"))
const PrivateRoute = lazy(() => import ("./components/routes/Private"))
const Dashboard = lazy(() => import ("./pages/user/Dashboard"))
const ProfileUser = lazy(() => import ("./pages/user/ProfileUser"))
const Orders = lazy(() => import ("./pages/user/Orders"))
const AdminRoute = lazy(() => import ("./components/routes/AdminRoute"))
const AdminDashboard = lazy(() => import ("./pages/admin/AdminDashboard"))
const CreateCategory = lazy(() => import ("./pages/admin/CreateCategory"))
const CreateProduct = lazy(() => import ("./pages/admin/CreateProduct"))
const Products = lazy(() => import ("./pages/admin/Products"))
const UpdateProduct = lazy(() => import ("./pages/admin/UpdateProduct"))
const HomePage = lazy(() => import('./pages/HomePage'))
const AboutPage = lazy(() => import('./pages/AboutPage'))
const ContactPage = lazy(() => import('./pages/ContactPage'))
const OurStory = lazy(() => import('./pages/OurStory'))
const LabdoorPage = lazy(() => import('./pages/LabdoorPage'))
const ErrorPage = lazy(() => import('./pages/ErrorPage'))
const CartPage = lazy(() => import('./pages/CartPage'))
const ViewAllProducts = lazy(() => import('./pages/ViewAllProducts'))
const SearchPage = lazy(() => import('./pages/SearchPage'))
const SinglePage = lazy(() => import('./pages/SinglePage'))
const Category = lazy(() => import('./pages/Category'))
const AdminOrders = lazy(() => import('./pages/admin/AdminOrders'))

const App = () => {
  return (
    <BrowserRouter>
      <Navbar />
      <Toaster />
      <Suspense fallback={<SuspenceFallback/>}>
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/signup" element={<SignUpPage />}/>

          <Route path="/profile" element={<PrivateRoute />}>
            <Route path="user" element={<Dashboard />}/>
            <Route path="user/userprofile" element={<ProfileUser />}/>
            <Route path="user/orders" element={<Orders />}/>
          </Route>

          <Route path="/profile" element={<AdminRoute />}>
            <Route path="admin" element={<AdminDashboard />} />
            <Route path="admin/create-category" element={<CreateCategory />} />
            <Route path="admin/create-product" element={<CreateProduct />} />
            <Route path="admin/products" element={<Products />} />
            <Route path="admin/admin-order" element={<AdminOrders />} />
            <Route path="admin/products/:slug" element={<UpdateProduct />} />
          </Route>

          <Route path="/login" element={<LoginPage />}/>
          <Route path="/forgotpassword" element={<ForgotPassword />}/>
          <Route path="/about" element={<AboutPage />}/>  
          <Route path="/contact" element={<ContactPage />}/>  
          <Route path="/ourstory" element={<OurStory />}/>  
          <Route path="/labdoorpage" element={<LabdoorPage />}/>
          <Route path="/*" element={<ErrorPage />}/>
          <Route path="/cart" element={<CartPage />}/>
          <Route path="/allproducts" element={<ViewAllProducts />}/>
          <Route path="/search" element={<SearchPage />}/>
          <Route path="/singlepage/:slug" element={<SinglePage />}/>
          <Route path="/category/:slug" element={<Category />}/>
        </Routes>
        <Footer />
      </Suspense>
    </BrowserRouter>
  )
}

export default App
